@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.App {
  display: block;
  margin: 0 auto;
  text-align: center;
  width: 95%;
}
.calendar {
    display: block;
    position: relative;
    width: 100%;
    background: #f5ffdb;
    border: 1px solid #eee;
}
div .header {
    padding: 10px 5px 5px 5px;
}

.row {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.col {
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
}

.col-start {
    justify-content: flex-start;
    text-align: left;
}

.col-center {
    justify-content: center;
    text-align: center;
}

.col-end {
    justify-content: flex-end;
    text-align: right;
}

.icon {
    font-family: 'Material Icons', serif;
    font-style: normal;
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-feature-settings: 'liga';
            font-feature-settings: 'liga';
}

.icon:hover {
    cursor: pointer;
}

.btn {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #33bdef), to(#019ad2));
    background:-webkit-linear-gradient(top, #33bdef 5%, #019ad2 100%);
    background:linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
    border-radius:4px;
    display:inline-block;
    cursor:pointer;
    color:#ffffff;
    font-size:14px;
    padding:5px 12px;
    text-decoration:none;
    margin-top: 3px;
    border: none;
}

.add_event:hover {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #019ad2), to(#33bdef));
    background:-webkit-linear-gradient(top, #019ad2 5%, #33bdef 100%);
    background:linear-gradient(to bottom, #019ad2 5%, #33bdef 100%);
}

.add_event_sm {
    float: left;
    margin-left: 5px;
    padding: 0 4px;
    border-radius: 10px;
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, rgba(51, 189, 239, 0.64)), to(rgba(1, 154, 210, 0.61)));
    background:-webkit-linear-gradient(top, rgba(51, 189, 239, 0.64) 5%, rgba(1, 154, 210, 0.61) 100%);
    background:linear-gradient(to bottom, rgba(51, 189, 239, 0.64) 5%, rgba(1, 154, 210, 0.61) 100%)
}

.add_event_sm:focus {
    outline: none;
}

.update {
    margin-left: 5px;
    cursor: not-allowed;
}

.search {
    outline: none;
    border: 1px solid #656565;
    border-radius: 3px;
}

.search:hover,:focus {
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 255, 0.13);
}
.days {
    text-transform: uppercase;
    font-weight: 400;
    color: #494949;
    font-size: 11px;
    padding: 10px 0;
    border-top: 1px solid #eee;
}
.body .cell {
    position: relative;
    height: 5em;
    border-right: 1px solid #eee;
    overflow: hidden;
    cursor: pointer;
    background: #fff;
    -webkit-transition: 0.25s ease-out;
    transition: 0.25s ease-out;
}

.body .cell:hover {
    background: #f5ffdb;
    border-right: 20px solid rgba(169, 107, 238, 0.65);
    -webkit-transition: 0.5s ease-out;
    transition: 0.5s ease-out;
}

.body .today {
    background: rgba(114, 187, 255, 0.4);
}

.body .events{
    width: 100%;
    height: 100%;
    background: rgba(248, 249, 117, 0.61);
    font-style: italic;
    overflow-y: auto;
}
.body .bold {
    font-weight: bold;
}

.body .row {
    border-bottom: 1px solid #eee;
}

.body .cell .number {
    position: absolute;
    font-size: 82.5%;
    line-height: 1;
    top: .75em;
    right: 1.3em;
    font-weight: 700;
}

.body .disabled {
    color: #ccc;
    pointer-events: none;
}

.body .cell .bg {
    font-weight: 700;
    line-height: 1;
    color: #1a8fff;
    opacity: 0;
    font-size: 8em;
    position: absolute;
    top: -.2em;
    right: -.05em;
    -webkit-transition: .25s ease-out;
    transition: .25s ease-out;
    letter-spacing: -.07em;
}

.body .cell:hover .bg, .calendar .body .selected .bg  {
    opacity: 0.5;
    -webkit-transition: .5s ease-in;
    transition: .5s ease-in;
}

.body .col {
    flex-grow: 0;
    flex-basis: calc(100%/7);
    width: calc(100%/7);
}
.popupForm {
    padding: 10px 15px 10px 5px;
    width: 300px;
}

.popup_input {
    width: 100%;
    outline: none;
    border: 1px solid #656565;
    border-radius: 3px;
}

.popup_btn {
    float: left;
    margin-bottom: 20px;
    margin-left: 10px;
}

.popup_description {
    float: left;
    font-size: 14px;
}

.btn_add {
    margin-right: 15px;
}

.btn_add, .btn_delete {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #33bdef), to(#019ad2));
    background:-webkit-linear-gradient(top, #33bdef 5%, #019ad2 100%);
    background:linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
    border-radius:4px;
    display:inline-block;
    cursor:pointer;
    color:#ffffff;
    font-size:14px;
    padding:4px 12px;
    text-decoration:none;
    border: none;
}

.btn_add:hover {
    cursor: pointer;
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #019ad2), to(#33bdef));
    background:-webkit-linear-gradient(top, #019ad2 5%, #33bdef 100%);
    background:linear-gradient(to bottom, #019ad2 5%, #33bdef 100%);
}

.btn_delete:hover{
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #019ad2), to(#33bdef));
    background:-webkit-linear-gradient(top, #019ad2 5%, #33bdef 100%);
    background:linear-gradient(to bottom, #019ad2 5%, #33bdef 100%);
}
